.boxmodel {
  width: 450px;
  margin: auto;
  margin-top: 70px;
  margin-bottom: 40px;
}
.cancel {
  margin-left: auto;
  align-self: center;
  margin-right: 10px;
}
.cancel img {
  cursor: pointer;
}
.boxmodel_body {
  padding: 30px 30px;
  height: 250px;
  background-color: white;
}
.bodytext p {
  margin: 0;
  text-align: center;
  color: rgba(91, 91, 91, 255);
  font-family: 'Open Sans', sans-serif;
  font-weight: 400;
  font-size: 14px;
}
.boxmodel form {
  display: flex;
  flex-direction: column;
  text-align: center;
}
.boxmodel form input[type='email'],
input[type='password'] {
  height: 50px;
  border: none;
  border-bottom: 1px solid grey;
  text-align: center;
  padding-top: 20px;
  margin-top: 20px;
  color: rgba(91, 91, 91, 255);
  font-family: 'Open Sans', sans-serif;
  font-weight: 700;
}
.boxmodel form input:focus {
  outline: none;
}
.boxmodel form input::placeholder {
  text-align: center;
  font-size: 12px;
  font-weight: 400;
}

.boxmodel form input[type='submit'] {
  height: 40px;
  border: none;
  border-radius: 10px;
  text-align: center;
  color: white;
  font-family: 'Open Sans', sans-serif;
  font-weight: 700;
  background-color: var(--default-color);
  cursor: pointer;
  margin: 40px 0px 40px 0px;
}
.boxmodel_body2 {
  display: flex;
  align-items: center;
  justify-content: center;
}
.boxmodel_body3 form input[type='password'] {
  margin: 0;
}
.boxmodel_body3 form input[type='submit'] {
  margin: 20;
}
.forgetPassword {
  position: fixed;
  left: 0;
  bottom: 0;
  top: 0px;
  right: 0;
  background: rgba(67, 70, 74, 0.7);
  /* box-shadow: 50px 50px 10px #aaaaaa; */
}
.box {
  margin-top: 190px;
}
.boxmodel button {
  // margin: auto;
  margin-top: 40px;
  // width: 50%;
}
